import { LogsService } from './../../services/logs.service';
import { Component, Inject, OnInit } from '@angular/core';
import { ToastrService } from 'node_modules/ngx-toastr';
import { Router } from '@angular/router';
import { AuthService } from '../../services/auth.service';
import { AppService } from '../../services/app.service';
import { MySharedService } from '../../services/sharedService';
import { CustomerService } from '../../services/customer.service';
import { DOCUMENT } from '@angular/platform-browser';
import { Applicant } from '../../models/applicant';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { BioService } from '../../services/bio.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})

export class LoginComponent implements OnInit {

  @BlockUI() blockUI: NgBlockUI;
  user: any = {};
  bio: any = {};
  bios: any = [];
  response: any;
  usermodel: any = {};
  afisAuth: any = {};
  fingerprint = [];
  verifyUser: any = {};
  menus: any;
  storageObject: any = {};

  userAssignedRights: any;

  constructor(private authService: AuthService, private appService: AppService, private toastr: ToastrService,
    private router: Router, private apiService: BioService, private globalService: MySharedService,
    private logs: LogsService,
    private custSvc: CustomerService, @Inject(DOCUMENT) private document: any) {
    this.user.username = '';
    this.user.password = '';
  }

  loginform = true;
  recoverform = false;
  ngOnInit() {
    const localData = localStorage.getItem('bio.glob#$$#')

    console.log("LocalData::", localData)

    if (!localData) {
      this.getGlobals();
    }
    // console.log("Initialization:  " + localStorage.getItem('bio.glob#$$#'));
    // localStorage.getItem('bio.glob#$$#') ? this.doNothing() : this.getGlobals();
  }
  showRecoverForm() {
    this.loginform = !this.loginform;
    this.recoverform = !this.recoverform;
  }
  log(userId, activity) {
    const log = {
      'userId': userId,
      'activity': activity
    };
    this.logs.log(log).subscribe((data) => {

    }, error => {
      this.blockUI.stop();
      // return this.toastr.error('Error logging.', 'Error!', { timeOut: 4000 });
    });
  }
  login() {
    if (this.user.username === '') {
      return this.toastr.warning('Please specify the user name', 'Alert!', { timeOut: 1500 });
    } else if (this.user.password === '') {
      return this.toastr.warning('Please specify the password', 'Alert!', { timeOut: 1500 });
    } else {

      console.log(this.user);

      this.authService.login(this.user).subscribe(res => {
        // this.blockUI.start('Authenticating user...');
        this.response = JSON.parse(res);
        console.log('auth res :: '+ this.response);
        if (this.response.status === false) {
          this.log(0, 'failed to log in ' + this.user.username);
          this.blockUI.stop();
          return this.toastr.warning(this.response.respMessage, 'Alert!', { timeOut: 1500 });
        }
        if (this.response.status === true) {
          console.log("Logged in successfully")
          this.appService.getUserAssignedRights(this.response.model.group).subscribe(resp => {
            console.log("UserRIghts::", resp)
            this.userAssignedRights = resp;
            if (this.userAssignedRights.status === true) {
              this.log(this.response.model.id, 'logged in');
              this.appService.getUserMenus(this.response.model.group).subscribe(resp => {
                this.menus = resp;
                console.log("UserMenus::", this.menus)

                if (this.menus.status === true) {
                  this.storageObject.username = this.user.username;
                  this.storageObject.rightId = this.response.model.id;
                  this.storageObject.rights = this.menus.hashset;
                  this.storageObject.branch = this.response.model.branch;
                  this.storageObject.group = this.response.model.group;
                  this.storageObject.userAssignedRights = this.userAssignedRights.hashset;

                  localStorage.setItem('otc', JSON.stringify(this.storageObject));
                  this.globalService.setAuth(true);
                  this.globalService.setRightId(this.user.id);
                  this.globalService.setUsername(this.user.username);
                  this.globalService.setRights(this.menus.hashset);
                  this.globalService.setBranch(this.response.model.branch);
                  this.globalService.setGroup(this.response.model.group);
                  this.globalService.setUserAssignedRights(this.userAssignedRights.hashset);
                  this.router.navigate(['/dashboard']);
                  this.getGlobals();
                } else {
                  this.storageObject.username = this.user.username;
                  this.storageObject.rightId = this.user.id;
                  this.storageObject.rights = this.menus.hashset;
                  this.storageObject.branch = this.user.branch;
                  this.storageObject.group = this.response.model.group;
                  this.storageObject.userAssignedRights = this.userAssignedRights.hashset;

                  localStorage.setItem('otc', JSON.stringify(this.storageObject));
                  this.globalService.setAuth(true);
                  this.globalService.setRightId(this.user.id);
                  this.globalService.setUsername(this.user.username);
                  this.globalService.setRights(this.menus.hashset);
                  this.globalService.setBranch(this.response.model.branch);
                  this.globalService.setGroup(this.response.model.group);
                  this.globalService.setUserAssignedRights(this.userAssignedRights.hashset);
                  this.getGlobals();
                  this.router.navigate(['/dashboard']);
                  //  this.blockUI.stop();
                  return this.toastr.warning(this.menus.respMessage, 'Alert!', { timeOut: 1500 });
                }
              });
            } else {
              this.blockUI.stop();
              return this.toastr.warning(this.response.respMessage, 'User Assigned Rights not fetched!', { timeOut: 1500 });
            }
          },
            err => {
              console.log("Error:::", err)
              console.log("sss", err.error)
              const error = this.globalService.decryptData(err.error.text)
              console.log("err", error)
            }
          );
        }
      }, error => {
        this.log(0, 'server error ' + this.user.username);
        this.blockUI.stop();
        return this.toastr.error('Error in loading data.', 'Error!', { timeOut: 1500 });
      });
    }
  }

  loginV2() {
    if (this.user.username === '') {
      return this.toastr.warning('Please specify the user name', 'Alert!', { timeOut: 1500 });
    } else if (this.user.password === '') {
      return this.toastr.warning('Please specify the password', 'Alert!', { timeOut: 1500 });
    } else {
      this.authService.loginV2(this.user).subscribe(res => {
        const authRes = JSON.parse(res);

        console.log("Authres", authRes)

        if (authRes.access_token && authRes.access_token !== '') {
          localStorage.setItem('auth', JSON.stringify({
            ...authRes,
            loginType: 'manual'
          }))
          console.log('authres - loginType', JSON.parse(localStorage.getItem('auth')));
          // get user
          this.appService.getUser().subscribe((user: any) => {
            console.log("User::", user)
            const res = JSON.parse(user)
            if (res.status) {
              this.response = JSON.parse(user);

              console.log(this.response);

              this.appService.getUserAssignedRights(this.response.model.group).subscribe((userRights: any) => {
                this.userAssignedRights = JSON.parse(userRights);
                if (this.userAssignedRights.status === true) {
                  this.log(this.response.model.id, 'logged in');
                  this.appService.getUserMenus(this.response.model.group).subscribe(resp => {
                    this.menus = JSON.parse(resp);
                    if (this.menus.status === true) {
                      this.storageObject.username = this.user.username;
                      this.storageObject.rightId = this.response.model.id;
                      this.storageObject.rights = this.menus.hashset;
                      this.storageObject.branch = this.response.model.branch;
                      this.storageObject.country=this.response.model.country;
                      this.storageObject.teller=this.response.model.teller;
                      this.storageObject.group = this.response.model.group;
                      this.storageObject.userAssignedRights = this.userAssignedRights.hashset;
                      localStorage.setItem('otc', JSON.stringify(this.storageObject));
                      this.globalService.setAuth(true);
                      this.globalService.setRightId(this.user.id);
                      this.globalService.setUsername(this.user.username);
                      this.globalService.setRights(this.menus.hashset);
                      this.globalService.setBranch(this.response.model.branch);
                      this.globalService.setGroup(this.response.model.group);
                      this.globalService.setUserAssignedRights(this.userAssignedRights.hashset);
                      this.router.navigate(['/dashboard']);
                      this.getGlobals();
                    } else {
                      this.storageObject.username = this.user.username;
                      this.storageObject.rightId = this.user.id;
                      this.storageObject.rights = this.menus.hashset;
                      this.storageObject.branch = this.user.branch;
                      this.storageObject.group = this.response.model.group;
                      this.storageObject.userAssignedRights = this.userAssignedRights.hashset;
                      localStorage.setItem('otc', JSON.stringify(this.storageObject));
                      this.globalService.setAuth(true);
                      this.globalService.setRightId(this.user.id);
                      this.globalService.setUsername(this.user.username);
                      this.globalService.setRights(this.menus.hashset);
                      this.globalService.setBranch(this.response.model.branch);
                      this.globalService.setGroup(this.response.model.group);
                      this.globalService.setUserAssignedRights(this.userAssignedRights.hashset);
                      this.getGlobals();
                      this.router.navigate(['/dashboard']);
                      //  this.blockUI.stop();
                      return this.toastr.warning(this.menus.respMessage, 'Alert!', { timeOut: 1500 });
                    }
                  }, error => {
                    //  console.log("Error::", this.globalService.decryptData(error.error.text))
                    return this.toastr.warning('Error getting User Menus', 'Alert!', { timeOut: 1500 });
                  });
                } else {
                  this.blockUI.stop();
                  return this.toastr.warning(this.response.respMessage, 'User Assigned Rights not fetched!', { timeOut: 1500 });
                }
              }, error => {
                console.log("Error!!:", error)
                const ppp = this.globalService.decryptData(error.error)
                console.log("err", ppp)
                return this.toastr.warning('Error getting User Rights', 'Alert!', { timeOut: 1500 });
              })

            } else {
              return this.toastr.warning('No User Found', 'Alert!', { timeOut: 1500 });
            }

          }, error => {
            console.log("Error::", error)
            return this.toastr.warning('No User Details Found', 'Alert!', { timeOut: 1500 });
          })
        } else {
          this.log(0, 'failed to log in ' + this.user.username);
          this.blockUI.stop();
          return this.toastr.warning(authRes.message, 'Alert!', { timeOut: 1500 });
        }

      },
        error => {
          console.log("Error::", error)
          return this.toastr.warning(`Error logging in: ${error.ad}`, 'Alert!', { timeOut: 1500 });
        }
      );
    }
  }

  captureFP() {
    if (this.user.username === '') {
      this.toastr.warning('Please specify the user name to proceed', 'Warning!', { timeOut: 1500 });
    }
    this.apiService.capturePrint().subscribe(data => {
      this.bio = data;
      console.log("fingerprint received: " + this.bio);
      if (this.bio.ErrorCode === 0) {

        if (this.bio !== null && this.bio.BMPBase64.length > 0) {
          this.document.getElementById('finger').src =
            'data:image/bmp;base64,' + this.bio.BMPBase64;
          this.verifyUser.singlePrint = {
            position: 0,
            fingerPrint: this.bio.BMPBase64
          };
          this.verifyUser.transactionType = 'L';
          this.verifyUser.customerId = this.user.username;
          this.verifyUser.transactionId = '';
          this.afisVerifyUser();
        }

      } else {
        return this.toastr.warning('failed to Capture finger print, please retry', 'Warning!');
      }
    });
  }

  afisVerifyUser() {
    if (this.user.username === '') {
      return this.toastr.warning('Please specify the user name to continue', 'Alert!', { timeOut: 1500 });
    } else {
      this.blockUI.start('Verifying user finger print...');
      this.apiService.afisLogin(this.verifyUser).subscribe((dat: any) => {
        const authRes = JSON.parse(dat);
        console.log("authRes", authRes)
        if (authRes.json.status) {
          if (authRes.token.access_token && authRes.token.access_token !== '') {
            localStorage.setItem('auth', JSON.stringify({
              ...authRes.token,
              loginType: 'bio',
            }))

            this.appService.getUser().subscribe((user: any) => {
              console.log("User::", user)
              const res = JSON.parse(user)
              if (res.status) {
                this.response = JSON.parse(user);
                this.printAuth();
              } else {
                this.blockUI.stop();
                return this.toastr.warning('No User Found', 'Alert!', { timeOut: 1500 });
              }

            }, error => {
              console.log("Error::", error)
              return this.toastr.warning('No User Details Found', 'Alert!', { timeOut: 1500 });
            })

          } else {
            this.blockUI.stop();
            return this.toastr.warning(authRes.json.message, 'Warning!', { timeOut: 3000 });
          }
        } else {
          this.blockUI.stop();
          return this.toastr.warning(authRes.json.message, 'Warning!', { timeOut: 3000 });
        }
      }, error => {
        this.log(0, `Error: ${error}` + this.user.username);
        this.blockUI.stop();
        return this.toastr.warning('Failed to contact ABIS Client', 'Alert!', { timeOut: 1500 });
      })
    }
  }


  printAuth() {
    // this.user.customerId = '';
    // this.user.transactionType = 'L';
    this.authService.printAuth(this.user).subscribe(res => {
      this.response = JSON.parse(res);

      if (this.response.status === false) {
        return this.toastr.warning(this.response.respMessage, 'Alert!', { timeOut: 1500 });
      }
      if (this.response.status === true) {
        this.appService.getUserAssignedRights(this.response.model.group).subscribe(resp => {
          this.userAssignedRights = JSON.parse(resp);
          console.log(res);
          if (this.userAssignedRights.status === true) {
            this.appService.getUserMenus(this.response.model.group).subscribe(resp => {
              this.menus = JSON.parse(resp);
              this.blockUI.stop();
              if (this.menus.status === true) {
                this.storageObject.username = this.user.username;
                this.storageObject.rightId = this.response.model.id;
                this.storageObject.rights = this.menus.hashset;
                this.storageObject.branch = this.response.model.branch;
                this.storageObject.group = this.response.model.group;
                this.storageObject.userAssignedRights = this.userAssignedRights.hashset;

                localStorage.setItem('otc', JSON.stringify(this.storageObject));
                this.globalService.setAuth(true);
                this.globalService.setRightId(this.user.id);
                this.globalService.setUsername(this.user.username);
                this.globalService.setRights(this.menus.hashset);
                this.globalService.setBranch(this.response.model.branch);
                this.globalService.setGroup(this.response.model.group);
                this.globalService.setUserAssignedRights(this.userAssignedRights.hashset);
                this.router.navigate(['/dashboard']);
              } else {
                this.storageObject.username = this.user.username;
                this.storageObject.rightId = this.user.id;
                this.storageObject.rights = this.menus.hashset;
                this.storageObject.branch = this.user.branch;
                this.storageObject.group = this.response.model.group;
                this.storageObject.userAssignedRights = this.userAssignedRights.hashset;
                localStorage.setItem('otc', JSON.stringify(this.storageObject));
                this.globalService.setAuth(true);
                this.globalService.setRightId(this.user.id);
                this.globalService.setUsername(this.user.username);
                this.globalService.setRights(this.menus.hashset);
                this.globalService.setBranch(this.response.model.branch);
                this.globalService.setGroup(this.response.model.group);
                this.globalService.setUserAssignedRights(this.userAssignedRights.hashset);
                this.router.navigate(['/dashboard']);
                return this.toastr.warning(this.menus.respMessage, 'Alert!', { timeOut: 1500 });
              }
            });
          } else {
            this.blockUI.stop();
            return this.toastr.warning(this.response.respMessage, 'User Assigned Rights not fetched!', { timeOut: 1500 });
          }
        }, error => {
          this.blockUI.stop();
          return this.toastr.error('Error in loading data.', 'Error!', { timeOut: 1500 });
        });
      }
    });
  }

  getGlobals() {
    this.authService.getGlobals().subscribe(data => {
      localStorage.setItem('bio.glob#$$#', data);
    }, error => {
      console.log("Error:", error)
      return this.toastr.error('Error in loading configs.', 'Error!', { timeOut: 1500 });
    });
  }

  doNothing() {

  }
}
